.faq-box {
    width: 100%;
    height: 100%;
    background-color: var(--secondaryColor);
}

.faq-box>ul {
    list-style: none;
}

.faq-list-item{
    padding: 1rem;
}

.faq-list-item>input {
    display: none;
}

.faq-list-item>label {
    color: var(--secondaryTxt);
    font-weight: 900;
}

.faq-list-item>label::before {
    content: '+';
    margin-right: 1rem;
    font-weight: 900;
}

.faq-list-item>input:checked+label::before{
    content: '-';
    font-weight: 900;
}

.faq-list-item>p {
    /* margin-top: .6rem; */
    color: var(--primaryTxt);
    display: none;
    max-width: 80%;
    padding: .6rem;
}

.faq-list-item>input:checked+label+p {
    display: block;
}
