.settings-box {
    width: 100%;
    height: 100%;
    background-color: var(--secondaryColor);
}

.settings-box>ul {
    list-style-type: none;
}

.settings-list-item {
    padding: 1rem;
    color: var(--secondaryTxt);
    font-weight: 600;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.btn {
    padding: .5rem 1rem;
    color: var(--secondaryTxt);
    background-color: var(--secondaryColor);
    border: .1rem solid var(--primaryColor);
}